import api from "../../../utils/api.js";
export default {
	data() {
		return {
			nickName:''
		};
	},
	onLoad(options) {
		this.$data.nickName = options.nickName;
	},
	methods:{
		clear(){
			this.$data.nickName = '';
		},
		confirm(){
			api.changeNickName({
				method: "POST",
				data: {
					nick_name: this.nickName
				},
			}).then((res)=>{
				if (res.code == 0) {
					this.$store.commit('updateNickName',this.nickName);
					uni.navigateBack({
						delta:1
					})
				}
				console.log(res)
			})
			
		}
	}
}
